import "./styles.css";
import React from "react";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      toggle: true,
      player1: [],
      player2: [],
      ansArr: [],
      winner: "",
      arr: [
        {
          id: 0,
          value: "10",
        },
        {
          id: 1,
          value: "10",
        },
        {
          id: 2,
          value: "10",
        },
        {
          id: 3,
          value: "10",
        },
        {
          id: 4,
          value: "10",
        },
        {
          id: 5,
          value: "10",
        },
        {
          id: 6,
          value: "10",
        },
        {
          id: 7,
          value: "10",
        },
        {
          id: 8,
          value: "10",
        },
      ],
    };
  }
  componentDidMount = () => {};
  handleClick = (id) => {
    this.handleValueChange(id);
  };
  handleValueChange = (id) => {
    if (this.state.ansArr.length < 9 && this.state.ansArr.indexOf(id) === -1) {
      if (this.state.toggle) {
        document.getElementById(`block${id}`).style.backgroundColor = "yellow";
        document.getElementById(`block${id}`).innerHTML = "X";
      } else {
        document.getElementById(`block${id}`).style.backgroundColor = "green";
        document.getElementById(`block${id}`).innerHTML = "0";
      }
      let newArr = this.state.arr.map((item, _id) => {
        let _item = item;
        if (id == _id) {
          _item.value = this.state.toggle;
        }
        return _item;
      });
      this.setState({
        toggle: !this.state.toggle,
        ansArr: [...this.state.ansArr, id],
        arr: newArr,
      });
      if (this.state.toggle) {
        this.setState({
          player1: [...this.state.player1, id],
        });
      } else {
        this.setState({
          player2: [...this.state.player2, id],
        });
      }
    }
  };
  componentDidUpdate = (prevProps, prevState, snapshot) => {
    let f = false;
    let _winner = "";
    if (
      (this.state.arr[0].value == true &&
        this.state.arr[4].value == true &&
        this.state.arr[8].value == true) ||
      (this.state.arr[0].value == true &&
        this.state.arr[1].value == true &&
        this.state.arr[2].value == true) ||
      (this.state.arr[2].value == true &&
        this.state.arr[4].value == true &&
        this.state.arr[6].value == true) ||
      (this.state.arr[0].value == true &&
        this.state.arr[3].value == true &&
        this.state.arr[6].value == true) ||
      (this.state.arr[8].value == true &&
        this.state.arr[4].value == true &&
        this.state.arr[0].value == true) ||
      (this.state.arr[2].value == true &&
        this.state.arr[5].value == true &&
        this.state.arr[8].value == true) ||
      (this.state.arr[6].value == true &&
        this.state.arr[7].value == true &&
        this.state.arr[8].value == true) ||
      (this.state.arr[3].value == true &&
        this.state.arr[4].value == true &&
        this.state.arr[5].value == true) ||
      (this.state.arr[1].value == true &&
        this.state.arr[4].value == true &&
        this.state.arr[7].value == true) ||
      (this.state.arr[8].value == false &&
        this.state.arr[4].value == false &&
        this.state.arr[0].value == false) ||
      (this.state.arr[0].value == false &&
        this.state.arr[4].value == false &&
        this.state.arr[8].value == false) ||
      (this.state.arr[2].value == false &&
        this.state.arr[4].value == false &&
        this.state.arr[6].value == false) ||
      (this.state.arr[2].value == false &&
        this.state.arr[5].value == false &&
        this.state.arr[8].value == false) ||
      (this.state.arr[0].value == false &&
        this.state.arr[1].value == false &&
        this.state.arr[2].value == false) ||
      (this.state.arr[6].value == false &&
        this.state.arr[7].value == false &&
        this.state.arr[8].value == false) ||
      (this.state.arr[3].value == false &&
        this.state.arr[4].value == false &&
        this.state.arr[5].value == false) ||
      (this.state.arr[1].value == false &&
        this.state.arr[4].value == false &&
        this.state.arr[7].value == false) ||
      (this.state.arr[0].value == false &&
        this.state.arr[3].value == false &&
        this.state.arr[6].value == false)
    ) {
      f = true;
      _winner =
        this.state.player1.length > this.state.player2.length
          ? "Winner is  Player 1 "
          : "Winner is  Player 2 ";

      // setTimeout(() => window.location.reload(), 3000);
    } else if (this.state.ansArr.length == 9) {
      _winner = "Its is draw";
      f = true;
      // window.location.reload();
    }
    if (prevState.ansArr !== this.state.ansArr && _winner != "") {
      this.setState(
        {
          winner: _winner,
        }
        // () => window.location.reload()
      );
      console.log(this.state);
    }
  };
  handleAnsString = (str, player) => {};
  handleGameReset = () => {
    window.location.reload();
  };
  render() {
    return (
      <>
        <h1 className="text-center">Welcome to Tic Tac Toe</h1>
        <div className="App">
          <div className="container">
            {this.state.arr.map((item) => {
              return (
                <div
                  id={`block${item.id}`}
                  className={`block block${item.id}`}
                  onClick={() => this.handleClick(item.id)}
                ></div>
              );
            })}
          </div>
          <div>
            <button onClick={this.handleGameReset}>RESET</button>
            <h2>RESULT: {this.state.winner != "" ? this.state.winner : ""}</h2>
          </div>
        </div>
      </>
    );
  }
}
export default App;
